import TeacherAvatar from '@/components/TeacherAvatar';
import { getTranslation } from '@/lib/i18n';

interface ContactForm {
  name: string;
  email: string;
  subject: string;
  category: string;
  message: string;
}

export default function ContactPage() {
  const [form, setForm] = useState<ContactForm>({
    name: '',
    email: '',
    subject: '',
    category: 'general',
    message: ''
  });
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submitted, setSubmitted] = useState(false);
  const [errors, setErrors] = useState<Partial<ContactForm>>({});
  const t = getTranslation('fr');

  const validateForm = (): boolean => {
    const newErrors: Partial<ContactForm> = {};

    if (!form.name.trim()) {
      newErrors.name = 'Le nom est requis';
    }

    if (!form.email.trim()) {
      newErrors.email = 'L\'email est requis';
    } else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(form.email)) {
      newErrors.email = 'Format d\'email invalide';
    }

    if (!form.subject.trim()) {
      newErrors.subject = 'Le sujet est requis';
    }

    if (!form.message.trim()) {
      newErrors.message = 'Le message est requis';
    } else if (form.message.length < 10) {
      newErrors.message = 'Le message doit contenir au moins 10 caract√®res';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!validateForm()) {
      return;
    }

    setIsSubmitting(true);

    // Simulate API call
    await new Promise(resolve => setTimeout(resolve, 2000));

    // Save to localStorage for demo purposes
    const contacts = JSON.parse(localStorage.getItem('contacts') || '[]');
    contacts.push({
      ...form,
      id: Date.now().toString(),
      submittedAt: new Date().toISOString()
    });
    localStorage.setItem('contacts', JSON.stringify(contacts));

    setIsSubmitting(false);
    setSubmitted(true);
    
    // Reset form
    setForm({
      name: '',
      email: '',
      subject: '',
      category: 'general',
      message: ''
    });
  };

  const updateForm = (field: keyof ContactForm, value: string) => {
    setForm(prev => ({ ...prev, [field]: value }));
    // Clear error when user starts typing
    if (errors[field]) {
      setErrors(prev => ({ ...prev, [field]: undefined }));
    }
  };

  if (submitted) {
    return (
      <div className="container mx-auto px-4 py-8">
        <div className="max-w-2xl mx-auto text-center space-y-6">
          <div className="text-6xl">‚úÖ</div>
          <h1 className="text-4xl font-bold">Message envoy√© !</h1>
          <p className="text-lg text-muted-foreground">
            Merci pour votre message. Notre √©quipe vous r√©pondra dans les plus brefs d√©lais.
          </p>
          
          <TeacherAvatar 
            locale="fr" 
            message="Merci de nous avoir contact√©s ! Nous avons bien re√ßu votre message et nous vous r√©pondrons rapidement. En attendant, pourquoi ne pas continuer √† apprendre ?" 
            animated={true}
          />
          
          <div className="flex gap-4 justify-center">
            <Button onClick={() => setSubmitted(false)} variant="outline">
              Envoyer un autre message
            </Button>
            <Button onClick={() => window.location.href = '/apprendre'}>
              Continuer l'apprentissage
            </Button>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="container mx-auto px-4 py-8">
      {/* Header */}
      <div className="text-center mb-8">
        <h1 className="text-4xl font-bold mb-4">{t.nav.contact}</h1>
        <p className="text-lg text-muted-foreground max-w-2xl mx-auto">
          Une question, une suggestion ou besoin d'aide ? Notre √©quipe est l√† pour vous accompagner 
          dans votre parcours d'apprentissage.
        </p>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
        {/* Contact Info */}
        <div className="space-y-6">
          {/* Contact Methods */}
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <span className="text-2xl">üìû</span>
                Nous Contacter
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="flex items-start gap-3">
                <span className="text-xl">üìß</span>
                <div>
                  <div className="font-medium">Email</div>
                  <div className="text-sm text-muted-foreground">support@codelearn.fr</div>
                </div>
              </div>
              
              <div className="flex items-start gap-3">
                <span className="text-xl">üí¨</span>
                <div>
                  <div className="font-medium">Chat en direct</div>
                  <div className="text-sm text-muted-foreground">Disponible 9h-18h</div>
                </div>
              </div>
              
              <div className="flex items-start gap-3">
                <span className="text-xl">üì±</span>
                <div>
                  <div className="font-medium">R√©seaux sociaux</div>
                  <div className="text-sm text-muted-foreground">@CodeLearnFR</div>
                </div>
              </div>
            </CardContent>
          </Card>

          {/* FAQ Quick Links */}
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <span className="text-2xl">‚ùì</span>
                Questions Fr√©quentes
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-3">
              <div className="space-y-2">
                <div className="font-medium text-sm">Comment fonctionne l'IA ?</div>
                <div className="text-xs text-muted-foreground">
                  Notre IA analyse votre code et fournit des corrections personnalis√©es.
                </div>
              </div>
              
              <div className="space-y-2">
                <div className="font-medium text-sm">Comment gagner plus d'XP ?</div>
                <div className="text-xs text-muted-foreground">
                  Compl√©tez des exercices, maintenez votre s√©rie et explorez diff√©rents langages.
                </div>
              </div>
              
              <div className="space-y-2">
                <div className="font-medium text-sm">Puis-je changer de langue ?</div>
                <div className="text-xs text-muted-foreground">
                  Oui, utilisez le s√©lecteur de langue en haut √† droite.
                </div>
              </div>
              
              <div className="space-y-2">
                <div className="font-medium text-sm">Comment d√©bloquer des badges ?</div>
                <div className="text-xs text-muted-foreground">
                  Les badges se d√©bloquent automatiquement selon vos progr√®s et r√©alisations.
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Teacher Avatar */}
          <div className="hidden lg:block">
            <TeacherAvatar 
              locale="fr" 
              message="Besoin d'aide ? N'h√©sitez pas √† nous contacter ! Notre √©quipe et moi sommes l√† pour vous accompagner dans votre apprentissage." 
              animated={true}
            />
          </div>
        </div>

        {/* Contact Form */}
        <div className="lg:col-span-2">
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <span className="text-2xl">‚úâÔ∏è</span>
                Envoyer un Message
              </CardTitle>
            </CardHeader>
            <CardContent>
              <form onSubmit={handleSubmit} className="space-y-6">
                {/* Name and Email */}
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div className="space-y-2">
                    <Label htmlFor="name">Nom *</Label>
                    <Input
                      id="name"
                      value={form.name}
                      onChange={(e) => updateForm('name', e.target.value)}
                      placeholder="Votre nom complet"
                      className={errors.name ? 'border-red-500' : ''}
                    />
                    {errors.name && (
                      <div className="text-sm text-red-500">{errors.name}</div>
                    )}
                  </div>
                  
                  <div className="space-y-2">
                    <Label htmlFor="email">Email *</Label>
                    <Input
                      id="email"
                      type="email"
                      value={form.email}
                      onChange={(e) => updateForm('email', e.target.value)}
                      placeholder="votre@email.com"
                      className={errors.email ? 'border-red-500' : ''}
                    />
                    {errors.email && (
                      <div className="text-sm text-red-500">{errors.email}</div>
                    )}
                  </div>
                </div>

                {/* Category and Subject */}
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div className="space-y-2">
                    <Label htmlFor="category">Cat√©gorie</Label>
                    <Select value={form.category} onValueChange={(value) => updateForm('category', value)}>
                      <SelectTrigger>
                        <SelectValue />
                      </SelectTrigger>
                      <SelectContent>
                        <SelectItem value="general">Question g√©n√©rale</SelectItem>
                        <SelectItem value="technical">Probl√®me technique</SelectItem>
                        <SelectItem value="account">Compte utilisateur</SelectItem>
                        <SelectItem value="feature">Suggestion de fonctionnalit√©</SelectItem>
                        <SelectItem value="bug">Signaler un bug</SelectItem>
                        <SelectItem value="partnership">Partenariat</SelectItem>
                      </SelectContent>
                    </Select>
                  </div>
                  
                  <div className="space-y-2">
                    <Label htmlFor="subject">Sujet *</Label>
                    <Input
                      id="subject"
                      value={form.subject}
                      onChange={(e) => updateForm('subject', e.target.value)}
                      placeholder="R√©sum√© de votre demande"
                      className={errors.subject ? 'border-red-500' : ''}
                    />
                    {errors.subject && (
                      <div className="text-sm text-red-500">{errors.subject}</div>
                    )}
                  </div>
                </div>

                {/* Message */}
                <div className="space-y-2">
                  <Label htmlFor="message">Message *</Label>
                  <Textarea
                    id="message"
                    value={form.message}
                    onChange={(e) => updateForm('message', e.target.value)}
                    placeholder="D√©crivez votre demande en d√©tail..."
                    rows={6}
                    className={errors.message ? 'border-red-500' : ''}
                  />
                  {errors.message && (
                    <div className="text-sm text-red-500">{errors.message}</div>
                  )}
                  <div className="text-xs text-muted-foreground text-right">
                    {form.message.length} caract√®res
                  </div>
                </div>

                {/* Submit Button */}
                <Button 
                  type="submit" 
                  className="w-full" 
                  disabled={isSubmitting}
                  size="lg"
                >
                  {isSubmitting ? (
                    <div className="flex items-center gap-2">
                      <div className="w-4 h-4 border-2 border-current border-t-transparent rounded-full animate-spin" />
                      Envoi en cours...
                    </div>
                  ) : (
                    'Envoyer le message'
                  )}
                </Button>
              </form>
            </CardContent>
          </Card>

          {/* Additional Info */}
          <Alert className="mt-6">
            <span className="text-lg">üí°</span>
            <AlertDescription>
              <strong>Conseil :</strong> Pour les questions techniques, n'h√©sitez pas √† inclure 
              des d√©tails sur votre navigateur, le langage de programmation utilis√© et 
              les √©tapes qui ont men√© au probl√®me.
            </AlertDescription>
          </Alert>
        </div>
      </div>

      {/* Response Time Info */}
      <div className="mt-12 text-center">
        <Card className="max-w-2xl mx-auto">
          <CardContent className="p-6">
            <h3 className="font-bold mb-2">Temps de r√©ponse</h3>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4 text-sm">
              <div>
                <div className="font-medium text-green-600">Questions g√©n√©rales</div>
                <div className="text-muted-foreground">24-48h</div>
              </div>
              <div>
                <div className="font-medium text-yellow-600">Probl√®mes techniques</div>
                <div className="text-muted-foreground">12-24h</div>
              </div>
              <div>
                <div className="font-medium text-red-600">Probl√®mes urgents</div>
                <div className="text-muted-foreground">2-6h</div>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
